import { useMemo, useState } from 'react';
import axios from 'axios';
import ClipLoader from 'react-spinners/ClipLoader';
import AdditionalInfo from 'components/AdditionalInfo/AdditionalInfo';
import JobDescription from 'components/JobDescription/JobDescription';
import Pagination from 'components/Pagination/Pagination';
import Button from 'react-bootstrap/Button';
import { Container, Row, Col } from 'react-bootstrap';
import {
  ListItem,
  List,
  ContentWrapper,
  Loader,
  Circle,
  ButtonGroup,
} from './jobBoard.styled';
import { getRandomColor, getAbbreviation } from 'helpers/helpers';

const JobBoard = () => {
  const [jobs, setJobs] = useState([]);
  const [page, setPage] = useState(1);
  const [error, setError] = useState(false);
  const [loading, setLoading] = useState(false);
  const [success, setSuccess] = useState(false);

  useMemo(() => {
    setLoading(true);
    axios
      .get(`https://www.themuse.com/api/public/jobs?page=${page}`)
      .then(res => {
        console.log('res', res.data);
        setJobs(res.data.results);
        setSuccess(true);
        setLoading(false);
      })
      .catch(err => {
        console.error('Error:', err);
        setError(true);
      });
  }, [page]);

  // console.log('jobs', jobs);

  return (
    <>
      {loading && (
        <Loader>
          <ClipLoader color="#3A4562" size={36} aria-label="Loading Spinner" />
        </Loader>
      )}
      {error && <div>Oops... Something went wrong</div>}

      <List>
        <Container>
          <Row>
            {success &&
              jobs &&
              jobs.map(job => (
                <Col md={6} key={job.id}>
                  <ListItem key={job.id} className="mb-2">
                    <ContentWrapper>
                      <Circle style={{ backgroundColor: getRandomColor() }}>
                        {getAbbreviation(job.name)}
                      </Circle>
                      <div>
                        <AdditionalInfo job={job} />
                        <JobDescription job={job} />
                      </div>
                    </ContentWrapper>
                    <ButtonGroup>
                      <Button
                        type="button"
                        variant="outline-secondary"
                        className="me-2"
                      >
                        Details
                      </Button>
                      <Button type="button" variant="outline-success">
                        Apply now
                      </Button>
                    </ButtonGroup>
                  </ListItem>
                </Col>
              ))}
          </Row>
          <Pagination setCurrentPage={setPage} />
        </Container>
      </List>
    </>
  );
};

export default JobBoard;
